.cart-heading{
    text-align: center;
    font-family: 'Heebo', sans-serif;
    text-transform: capitalize; 
}
.cart-content{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-around;
  
  }
  .cartitems-wrapper{
    display: flex;
    width:  80%;
    height: 280px;
    justify-content: space-around;
   
    border-radius: 5px;
    background-color:#ffb703; 
    margin-top: 20px;
    margin-left: 20px;
  }
  .cartitems-wrapper img{
      width: 350px;
      height: 280px;
      
  }
 
  .cartitems-wrapper-data{
      font-size: 15px;
      font-family: 'Heebo', sans-serif;
      text-transform: capitalize;
      padding-left: 10px;
  }
  .cartitems-wrapper-data button{
      border: 0;
      background-color: #04323d;
      color: white;
      height: 30px;
      width: 80px;
      margin-top: 5px;
      border-radius: 5px;
  }
  .cart-summary{
      width: 400px;
      height: 250px;
     
      font-family: 'Heebo', sans-serif;
      text-transform: capitalize;
      font-size: 30px;
      text-align: left;
      background-color:#04323d ;
      color: white;
      box-shadow: 5px 5px 5px grey;
      border-radius: 5px;
      text-align: center;
      padding-top: 10px;
      margin-top: 15px;
  }