@media screen and (max-width:1024px){
   
    .footer-container{
       
        display: flex;
        justify-content: space-around;
      font-size: 15px;
        color: white;
        
        padding-top: 40px;
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        
    }
     .footer-social-links img{
         width: 28px;
         height: 23px;
     }
     .footer-contact img{
        width: 28px;
        height: 23px; 
     }
}

/* 2ND media query */

@media screen and (max-width:770px){
    
    .footer-container{
       
        display: flex;
        justify-content: space-around;
      font-size: 14px;
        color: white;
        
        padding-top: 30px;
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        
    }
     .footer-social-links img{
         width: 28px;
         height: 23px;
         padding-top: 5px;
     }
     .footer-contact img{
        width: 28px;
        height: 23px; 
        padding-top: 5px;
     }
     .contact-footer-logo img{
        width: 80px;
        height: 50px;
        border-radius: 50%;
    }
    .footer-all-links img{
        width: 180px;
        height: 40px;
         }
}

/* 3rd media query */
@media screen and (max-width:560px){
    
    .footer-container{
       
        display: flex;
        justify-content: space-around;
      font-size: 10px;
        color: white;
        
        padding-top: 30px;
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        
    }
     .footer-social-links img{
         width: 25px;
         height: 20px;
         padding-top: 5px;
     }
     .footer-contact img{
        width: 25px;
        height: 20px; 
        padding-top: 5px;
     }
     .contact-footer-logo img{
        width: 60px;
        height: 50px;
        border-radius: 50%;
    }
    .footer-all-links img{
        width: 150px;
        height: 35px;
         }
}