 /* products page */

.wrapper{
    width: 100%;
    height: auto;;
}


 .all-item{
     
    font-family: 'Heebo', sans-serif;
    height: auto;
    margin-top: 30px;
    
    
 }
 .product-item{
    
     display: grid;
     /* grid-template-columns: 470px 470px 470px; */
     grid-template-columns: repeat(3,440px);
     grid-template-rows:  repeat(6,250px);
    column-gap: 0px;
    
    row-gap: 40px;
      justify-content: space-around;
 }


 .menu-item:hover {
    transform: scale(1.05)  ;
    transition: all 0.6s ;
    
 }
 
 .menu-item {
     border-radius:5px;
     box-shadow: 5px 5px 5px grey;
      background-color: #ffb703;
     display: flex;
     
     width: 440px;
     height: 250px;
     
       
}
.outer-item img{
     border-radius: 5px;
    height: 250px;
    width: 220px;
}
  
  
 .inner-item{
     padding-top: 12px;
     padding-left: 5px;
     text-transform: capitalize;
     font-family: 'Heebo', sans-serif;
     color: black;
 }
 .inner-item button{
    margin-top: 12px;
    width: 100px;
     height: 38px;
     border-radius: 5px;
     border: 0;
     text-transform: capitalize;
     font-family: 'Heebo', sans-serif;
     font-size: 15px;
     background-color: #04323d;
     color: white;
 } 
  

 