.main-about{
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(to right,rgba(2, 9, 75, 0.688),rgba(2, 9, 75, 0.543)) , url("C:\Users\akit7\reactfolder\techapp\public\Image\thumb-1920-959369 - Copy.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-attachment: fixed;
      
}
.about-main-heading{
    padding-top: 150px;
    color: white;
    font-size: 30px;
    text-transform: capitalize;
}
.about-txt{
    width: 100%;
    height: 100px;
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
    font-size: 22px;
    background-color: #04323d;
    color: white;
    
}
.about-container{
     padding-top: 20px;
     padding-left: 10px;
    display: grid;
    grid-template-columns: 330px 330px 330px 330px;
      justify-content: space-around;
      
      
}
.about-flex-data{
    box-shadow: 5px 5px 5px grey;
    border-radius: 10px;
    background-color: #ffb703;
}
 
.about-flex-data h2{
   
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
} 
.about-flex-data , h3, p{
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
} 

.about-section{
     width: 100%;
     height: 120px;

     background-color: #04323d;
     display: flex;
     justify-content: center;
     padding-top: 20px;
     margin-top: 20px;
     text-transform: capitalize;
     font-family: 'Heebo', sans-serif;
     font-size: 35px;
     color: aliceblue;
}
.about-footer{
    display: grid;
    grid-template-columns: 350px 350px 350px 350px;
    justify-content: space-around;
    padding-top: 30px;
    width: 100%;
    /* height: 330px; */
   
      
}
.about-footer img{
    border-radius: 10px;
    box-shadow: 5px 5px 5px grey;
}

.contact-header{
    padding-top: 20px;
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
    font-size: 35px;
    color: #ffb703;
    text-align: center;
    width: 100%;
    height: 120px;
    background-color: #04323d;
    
     
}
.contact-main{
    width: 100%;
    height: auto;
    display: flex;
     flex-wrap: wrap;
     margin-top: 20px;
     justify-content: space-around;
    /* display: grid;
    grid-template-columns: 700px 700px; */
}
.contact-main-flex-1st{
    
    height: 90vh;
    margin-top: 25px;
    padding-left: 35px;
    background-color: #285864;
    border-radius: 5px;
    border-radius: 5px 5px 5px grey;
    padding-top: 30px;
    
    
}
.contact-main-1st-button button{
    width: 120px;
    height: 40px;
   margin-left: 250px;
   margin-top: 20px;
   text-transform: capitalize;
   font-family: 'Heebo', sans-serif;
   font-size: 25px;
   border-radius: 10px;
   border: 0;
   background-color: #ffb703;
   
}


.contact-main-flex-2nd{
    margin-top: 25px;
    height: 90vh;
    margin-top: 25px;
    text-align: center;
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
    font-size: 25px;
    box-shadow: 5px 5px 5px grey;
    /* background-color: #bac021; */
    background-color: #285864;
    color: white;
}
.contact-main-flex-1st label{
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
   
    font-size: 20px;
    font-weight: 400;
    color: white;
 
}
.contact-form{
    width: 700px;
}
.contact-form input{
width: 600px;
height: 45px;
 
}
.contact-map-heading{
    text-transform: capitalize;
    font-family: 'Heebo', sans-serif;
    font-size: 25px;
    width: 100%;
    height: 120px;
 color: white;
  line-height: 120px;
 text-align: center;
    background-color: #04323d;
    margin-top: 20px;
}
.contact-map{
    display: flex;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
    justify-content: space-around;
    
    
}
.contact-delivary-image{
    width: 600px;
    height: 510px;
     
    margin-top: 20px;
}
.contact-map img{
    width: 600px;
    height: 500px;
    
    margin-top: 20px;
}

.mapouter{position:relative;text-align:right;height:500px;width:600px;}
.gmap_canvas {overflow:hidden;background:none!important;height:500px;width:600px;margin-top: 20px;}

