        width: 500px;
    }
    .contact-map{
        width: 100%;
    }
    
    .contact-form input{
    width: 450px;
    height: 45px;
    }
    .contact-delivary-image img{
        width: 500px;
        height: 510px;
        
        margin-top: 20px;
    }
    
    
    .mapouter{position:relative;text-align:right;height:500px;width:500px;}
    .gmap_canvas {overflow:hidden;background:none!important;height:500px;width:500px;margin-top: 20px;}
     
 }
