@media screen and (max-width:1400px) {
    .about-main-heading{
        padding-top: 150px;
        color: white;
        font-size: 30px;
        text-transform: capitalize;
    }
    .about-txt{
        width: 100%;
        height: 100px;
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        font-size: 22px;
        background-color: #04323d;
        color: white;
        
    }
    .about-container{
         padding-top: 20px;
         padding-left: 10px;
        display: grid;
        grid-template-columns: 300px 300px 300px 300px;
          justify-content: space-around;
          
          
    }
    .about-flex-data img{
        width: 300px;
    }
    .about-flex-data{
        box-shadow: 5px 5px 5px grey;
        border-radius: 10px;
        background-color: #ffb703;
    }
    .about-section{
        width: 100%;
        height: 120px;
   
        background-color: #04323d;
        display: flex;
        justify-content: center;
        padding-top: 20px;
        margin-top: 20px;
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        font-size: 35px;
        color: aliceblue;
   }
   .about-footer{
       display: grid;
       grid-template-columns: 300px 300px 300px 300px;
       justify-content: space-around;
       padding-top: 30px;
       /* width: 100%;
       height: 330px; */
      
         
   }
   .about-footer img{
       border-radius: 10px;
       box-shadow: 5px 5px 5px grey;
       width: 300px;
       
   }    
    
}
