 /* now we have apply 1st media query on home-page */
@media screen and (max-width:1360px) {
    .main-heading{
        font-size: 32px;
         padding-top: 120px;  
        font-family: 'Heebo', sans-serif;
        text-transform: capitalize;
        text-align: center;
    }
   
   .offers-products{
       font-family: 'Smooch', cursive;
   }
   
   
    .home-header-btn button{
        width: 140px;
        height: 38px;
        border-radius: 5px;
        border: 0;
        margin-left: 15px;
        margin-top: 10px;
        text-transform: capitalize;
        font-family: 'Heebo', sans-serif;
        font-size: 20px;
        color:black;
        background-color: rgb(59, 216, 230);
    }
    .product-item{
         display: grid;
         grid-template-columns: repeat(3,400px);
         grid-template-rows:  repeat(6,250px);
         column-gap: 0px;
       
         row-gap: 40px;
         justify-content: space-around;
    }
   
   
    .menu-item:hover {
       transform: scale(1.05)  ;
       transition: all 0.6s ;
       
    }
    
    .menu-item {
         border-radius:5px;
         box-shadow: 5px 5px 5px grey;
         background-color: #ffb703;
         display: flex;
         width: 400px;
        height: 250px;
           
   }
   .inner-item{
         padding-top: 12px;
         padding-left: 10px;
         text-transform: capitalize;
         font-family: 'Heebo', sans-serif;
         color: black;
         font-size: 16px;
}
.inner-item button{
         margin-top: 12px;
         width: 100px;
         height: 28px;
          border-radius: 5px;
         border: 0;
         text-transform: capitalize;
         font-family: 'Heebo', sans-serif;
         font-size: 15px;
         background-color: #04323d;
         color: white;
}
    
}
